.testing-border {
  border: 1px solid red !important;
}

//Reset
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  // background-image: url("https://homebrewery.naturalcrit.com/assets/parchmentBackground.jpg");
  background-image: linear-gradient(
    to bottom,
    rgb(0, 68, 255, 1),
    rgba(0, 102, 255, 0)
  );
  background-repeat: no-repeat;
  background-size: cover;
  display: grid;
  grid-template-rows: 10% 80% 10%;
  height: 100vh;
}

// Controls what the current active page on the navbar looks like
.active-page {
  display: flex;
  height: 100%;
}

// Controls what the current inactive pages on the navbar look like
.inactive-page {
  display: none;
}

.navbar {
  background-color: transparent !important;
}

hr {
  height: 0.15em;
  background-image: linear-gradient(
    to right,
    rgb(0, 68, 255),
    rgba(0, 102, 255, 0)
  );
  border: none;
  opacity: 100% !important;
}

// Prefixes for any flexbox needed, as recommended on https://css-tricks.com/snippets/css/a-guide-to-flexbox/

@mixin flexbox() {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
}

@mixin flex($values) {
  -webkit-box-flex: $values;
  -moz-box-flex: $values;
  -webkit-flex: $values;
  -ms-flex: $values;
  flex: $values;
}

@mixin order($val) {
  -webkit-box-ordinal-group: $val;
  -moz-box-ordinal-group: $val;
  -ms-flex-order: $val;
  -webkit-order: $val;
  order: $val;
}

.wrapper {
  @include flexbox();
}

.item {
  @include flex(1 200px);
  @include order(2);
}
